import React, { Component } from 'react';

// import 'webrtc-adapter';
import SFUClient from './sfusdk';
import api from './api';

class Video extends Component {
  sfu = null;
  video = null;
  state = {
    stream: null,
  }

  componentDidMount() {
    const { id } = this.props;
    api().post('/ticket', { direction: 'down', room: id })
      .then((ret) => {
        console.log('#### ticket created -> open ', ret.data.url);
        this.sfu = new SFUClient('down', ret.data.id);
        this.sfu.onclose = () => {
          console.log('#### closed');
          if (!this.video || !this.video.srcObject) return;
          this.video.srcObject = null;
        };
        // this.sfu.onaddstream = (event) => {
        //   console.log('onaddstrream', event)
        //   this.video.srcObject = event.stream;
        // };
        this.sfu.ontrack = (event) => {
          console.log('ontrack', event)
          if (event.track.kind === 'video') {
            this.video.srcObject = event.streams[0];
            this.setState({ stream: event.streams[0] })
          }
        };
        this.sfu.setMedia({ codec_type: 'VP9' }, { codec_type: 'OPUS' });
        this.sfu.connect(ret.data.url, ret.data.access_token);
      })
      .catch(error => console.log(error));
  }

  // componentDidUpdate() {
  //   this.video.srcObject = this.state.stream;
  // }

  render() {
    return (
      <div>
        <video autoPlay ref={(video) => { this.video = video; }} />
        {(this.state.stream !== null) ? this.state.stream.id : null}
      </div>
    )
  }
}

export default Video;



