import axios from 'axios';

const API_BASE = 'https://wh5aiqt8ji.execute-api.ap-northeast-1.amazonaws.com/dev/';

let adapter;

const api = () => {
  if (!adapter) {
    adapter = axios.create({
      baseURL: API_BASE,
    });
  }
  return adapter;
};

export default api;



