import React, { Component } from 'react';

import SFUClient from './sfusdk';
import api from './api';

class Broadcast extends Component {
  sfu = null;
  localStream = null;
  state = {
    streaming: false,
    connected: false,
  };

  onClick = () => {
    console.log('#### start broadcast()');
    const constraints = {
      video: {
        width: 640,
        height: 360,
        // frameRate: {
        //   max: 1,
        // },
      },
      audio: false,
    };
    navigator.mediaDevices.enumerateDevices()
      .then(mediaDevices => {
        mediaDevices.forEach(mediaDevice => {
          if (mediaDevice.kind === 'videoinput') {
            console.log(mediaDevice)
          }
        })
      })
    navigator.mediaDevices.getUserMedia(constraints)
      .then(stream => {
        console.log('#### getUserMedia() loaded');
        this.localStream = stream;
        this.setState({ streaming: true })
        return api().post('/ticket', { direction: 'up' })
      })
      .then(ret => {
        console.log('#### ticket created -> open ', ret.data.url);
        this.sfu = new SFUClient('up', ret.data.id);
        this.sfu.onclose = () => {
          if (this.localStream) {
            this.localStream.getTracks().forEach((track) => {
              if (track.kind === 'video') {
                console.log('stop track', track)
                track.stop();
              }
            })
            this.localStream = null;
          }
          console.log('#### closed');
          this.setState({ streaming: false })
          this.setState({ connected: false })
        };
        this.sfu.setMedia({ codec_type: 'VP9', bit_rate: 1000 }, { codec_type: 'OPUS' }, this.localStream);
        this.sfu.connect(ret.data.url, ret.data.access_token);
        this.setState({ connected: true })
      })
      .catch(error => {
        console.log(error);
        this.setState({ streaming: false })
        this.setState({ connected: false })
      });

  }
  onStop = () => {
    console.log('#### stop broadcast()');
    if (this.sfu) this.sfu.disconnect();
    // this.setState({ broadcasting: false })
  }

  render = () => {
    return (
      <React.Fragment>
        <button id="broadcast" disabled={this.state.streaming} onClick={this.onClick}>broadcast</button>
        <button id="stop" disabled={!this.state.connected} onClick={this.onStop}>Stop</button>
      </React.Fragment>
    )
  }
}

export default Broadcast;



